<template>
    <div>
        <fieldset>
            <legend>의원 검색</legend>
                <select v-model="party" multiple>
                    <option v-for="option in options" v-bind:key="option.party_cd">
                        {{option.party_cd}}
                    </option>
                </select>
                <select v-model="city" multiple>
                    <option v-for="option in options" v-bind:key="option.city_cd">
                        {{option.city_cd}}
                    </option>
                </select>
        </fieldset>
        <span> Selected: {{selected}}</span>
    </div>
</template>

<script>
    // import SearchComponenet from './components/Search'
    import axios from 'axios'
    export default {
        name: 'Search',
        data: function() {
            return {
                options: []
            }
        },
        methods: {
            searchTerm: function() {
                // using JSONPlaceholder
                const baseURI = 'localhost:3200';
                // this.$http.get(`${baseURI}/admin/pms`)
                axios.get(`${baseURI}/admin/pms`)
                .then((result) => {
                    console.log(result)
                    this.options = result.data
                })
            }
        }
    }
</script>

<style>
    
</style>



