<template>
    <div id="header">
        <label for="toggle" onclick>MENU</label>
        <input type="checkbox" id="toggle"/>
        <ul id="nav">
            <li><a href="#">Membership</a></li>
            <li><a href="#">Vote</a></li>
            <li><a href="#">PMS</a></li>
            <li><a href="#">CMS</a></li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'Header' 
}
</script>

<style>
    #header {
        background-color: white;
        color: #30b8ec;
        text-align: right;
        font-size: 20px;
    }
    a{
        background-color: white;
        text-decoration: none;
        color: #30b8ec;
    }
    label, #toggle {display: none;}
    #nav {
        display: inline;
        padding: 0;
        margin: 0;
    }
    #nav li {
        display: inline;
    }

    @media screen and (max-width: 480px){
        #nav { display: none; }
        #toggle:checked + #nav { display: block; }
        #nave li {
            display: block;
            width: 100%;
            text-align: center;
            border-top: 1px solid #ffffff;
            padding: 4px;
        }
        label {
            display: block;
            text-align: center;
            background: url('../assets/menu-button.png');
            background-repeat: no-repeat;
            background-position: 3%;
            background-size: 20px, 20px;
            font-size: 20px;
        }
    }
</style>



